.about-us {
  @media (min-width: 768px) {
    margin-top: 159px;
    margin-left: 58px;
  }

  @media (min-width: 1150px) {
    margin-top: 161px;
    margin-left: 58px;
  }
}

.about-us__wrapper {
  margin-top: -2px;
  display: block;

  @media (min-width: 768px) {
    .flex();
    margin-top: -191px;
    margin-left: -32px;
  }

  @media (min-width: 1150px) {
    margin-top: -157px;
    margin-left: 118px;
    .flex();
  }
}

.about-us__header {
  margin-top: 71px;
  margin-left: -1px;

  @media (min-width: 768px) {
    margin-top: 110px;
    margin-left: -1px;
  }

  @media (min-width: 1150px) {
    margin-left: 3px;
    margin-top: 108px;
  }
}

.about-us__title {
  font-family: @font700;
  font-size: 25px;
  font-weight: 700;
  color: #000000;
  line-height: 30px;

  @media (min-width: 768px) {
    font-size: 43px;
    line-height: 50px;
    margin-bottom: 0;
  }

  @media (min-width: 1150px) {
    font-size: 53px;
    margin-bottom: 0;
    margin-left: -1px;
  }
}

.about-us__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (min-width: 768px) {
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    width: 650px;
    margin-left: 0;
    justify-content: space-between;
    margin-top: 1px;
  }

  @media (min-width: 1150px) {
    margin-left: -46px;
    width: 650px;
    justify-content: space-between;
    margin-top: 0;
  }
}

.about-us__item--eco {
  width: 175px;

  @media (min-width: 768px) {
    margin: 0;
    width: auto;
  }

  @media (min-width: 1150px) {
    margin: 0;
    width: auto;
  }
}

.about-us__item--rate {
  width: 200px;

  @media (min-width: 768px) {
    order: 2;
    margin: 0;
    width: auto;
  }

  @media (min-width: 1150px) {
    margin: 0;
    width: auto;
  }
}

.about-us__item--likes {
  width: 181px;
  margin-top: 82px;

  @media (min-width: 768px) {
    order: 4;
    margin: 0;
    width: auto;
  }

  @media (min-width: 1150px) {
    margin: 0;
    width: auto;
  }
}

.about-us__item--handle {
  width: 211px;
  margin-top: 83px;

  @media (min-width: 768px) {
    order: 1;
    margin: 0;
    width: auto;
  }

  @media (min-width: 1150px) {
    margin: 0;
  }
}

.about-us__item--domestic {
  width: 251px;
  margin-top: 82px;

  @media (min-width: 768px) {
    order: 3;
    margin: 0;
    width: auto;
  }

  @media (min-width: 1150px) {
    margin: 0;
    width: auto;
  }
}

.about-us__item--package {
  width: 198px;
  margin-top: 84px;

  @media (min-width: 768px) {
    margin: 0;
    width: auto;
    order: 5;
  }

  @media (min-width: 1150px) {
    margin: 0;
  }
}

.about-us__item {
  font-family: @font700;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  margin-top: 83px;
  position: relative;

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 21px;
    margin-top: 0;
    width: 317px;
    margin-bottom: 113px;
  }

  @media (min-width: 1150px) {
    width: 300px;
    margin-bottom: 113px;
  }

  color: #63d1bb;

  &-eco-pic {
    position: absolute;
    top: -55px;
    left: 8px;
    width: 32px;
    height: 37px;
  }

  &-rate-pic {
    position: absolute;
    top: -55px;
    left: 0;
    width: 47px;
    height: 27px;
  }

  &-likes-pic {
    position: absolute;
    top: -48px;
    left: 7px;
    width: 34px;
    height: 27px;
  }

  &-handle-pic {
    position: absolute;
    top: -52px;
    left: 3px;
    width: 35px;
    height: 34px;
  }

  &-domestic-pic {
    position: absolute;
    top: -50px;
    left: -5px;
    width: 55px;
    height: 23px;
  }

  &-package-pic {
    position: absolute;
    top: -53px;
    left: 4px;
    width: 33px;
    height: 32px;
  }
}

.about-us__item-link {
  text-decoration: none;
  color: #000000;
}

.about-us__advantages {
  margin-top: 87px;

  @media (min-width: 768px) {
    margin-top: 103px;
    margin-left: 3px;
  }

  @media (min-width: 1150px) {
    margin-top: 106px;
    margin-left: 56px;
  }
}

.about-us__item-link--mode {
  @media (min-width: 768px) {
    .br;
  }
}
